.graphcontainer{
    background-color: black;
    border: 1px solid black;
    height: 330px;
    width: auto;
    border-radius: 5px;
}
.border{
    background-color: black;
    border: 2px solid rgb(44, 44, 44);
    padding:12px;
    border-radius: 10px;
}
.head{
    display: flex;
    justify-content: space-between;
    margin-top: 10px;
    margin-left: 10px;
    margin-right: 10px;
}
.start{
    color: white;
    font-weight: 600;
  
}

.end{
    color: white;
    font-size: 14px;
    width: 70%;
    display: flex;
    justify-content: space-around;
}